/* BusDisplay.css */
.bus-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    background: linear-gradient(to right, #1d5ca3, #1d5ca3);
    color: white;
    padding: 20px;
    text-align: center;
  }
  
  .bus-title {
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .bus-loading,
  .bus-error {
    font-size: 24px;
    color: #ffcccc;
  }
  
  .bus-list {
    list-style-type: none; /* Tar bort punkterna */
    padding: 0;
    max-width: 600px;
    width: 100%;
  }
  
  .bus-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    margin: 10px 0;
    border-radius: 10px;
    font-size: 24px;
    background: white;
    color: black;
    border: 2px solid rgba(255, 255, 255, 0.4);
  }
  
  .bus-item.warning {
    background: #ff5555;
    color: white;
  }
  
  .bus-line {
    font-weight: bold;
  }
  
  .bus-destination {
    font-style: italic;
  }
  