body {
  background-color: #1d5ca3; /* Mörkblå bakgrund */
  color: white;
  font-family: Arial, sans-serif;
  text-align: center;
}

h1 {
  font-size: rem;
  margin-bottom: 20px;
}

.bus-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
}

.bus-item {
  background: white;
  color: black;
  padding: 20px;
  border-radius: 10px;
  font-size: 1.5rem;
  width: 80%;
  max-width: 600px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bus-item.urgent {
  background-color: red;
  color: white;
}
